.contact {
    background: rgba(116, 125, 140,.5);
}

.contact__container {
    margin: 0 auto;
    max-width: 1300px;
    padding: 30px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.contact .contact__title {
    width: 200px;
    height: 70px;
    border-radius: 30px;
    font-size: 2rem;
    display: flex;
    color: rgba(255, 71, 87,1.0);
    justify-content: center;
    align-items: center;
    font-weight: bold;
}

.contact .contact__info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    line-height: 1.4rem;
    color: #000;
    opacity: .7;
}